.status{display:flex;align-items:center;flex-wrap:nowrap;gap:.75rem;font-size:var(--font-sub-size);font-weight:var(--font-sub-weight);line-height:var(--font-sub-height);letter-spacing:var(--font-sub-spacing);text-transform:uppercase}.status .dot{display:block;width:.375em;height:.375em;background-color:var(--foreground-color);border-radius:50%}.status .timer::before{content:"Seoul, "}.status .timer::after{content:attr(meridiem)}