.img{background:rgba(0,0,0,0);background-image:repeating-linear-gradient(135deg, var(--border-color), var(--border-color) 2px, var(--background-color) 2px, var(--background-color) 1rem);outline:2px solid var(--background-color);outline-offset:-1px;overflow:hidden;padding-top:56.25%}.img.-landscape{padding-top:28.125%}.img.-portrait{padding:unset;width:calc((100% - var(--grid-gap)*.5)*.25)}.img.-portrait img{left:auto;position:relative;top:auto;transform:unset}.img.-screen{padding-top:75%}.img.-square{padding-top:100%}.img div{display:flex;align-items:center;justify-content:center;left:50%;overflow:hidden;pointer-events:none;position:absolute;top:50%;transform:translate3d(-50%, -50%, 0);z-index:1}.img div span{color:var(--white-color);font-size:var(--font-size);font-weight:var(--font-weight);letter-spacing:var(--font-spacing);line-height:var(--font-height);opacity:0;text-transform:uppercase;text-wrap:nowrap;will-change:transform;transform:translate3d(0, 100%, 0);transition:all .6s var(--ease)}.img img{display:block;left:50%;position:absolute;top:50%;will-change:transform;transform:translate3d(-50%, -50%, 0);transition:all .6s var(--ease)}@media screen and (max-width: 1440px){.img.-landscape{padding-top:56.25%}}.-hover .img div span,.-hover .img img{transform:translate3d(0, 0, 0);opacity:1}.-hover .img img{filter:brightness(64%);transform:translate3d(-50%, -50%, 0) scale(108%)}video{background:rgba(0,0,0,0);background-image:repeating-linear-gradient(135deg, var(--border-color), var(--border-color) 2px, var(--background-color) 2px, var(--background-color) 1rem);outline:2px solid var(--background-color);outline-offset:-1px;overflow:hidden}