.cell{display:flex;flex-direction:column;gap:var(--grid-gap);width:100%}.cell.-between{flex-direction:row;justify-content:space-between;gap:unset}.cell.-center{align-items:center}.cell.-content{row-gap:var(--section-row)}.cell.-end{align-items:flex-end}.cell.-fill{grid-column:1/-1}.cell.-fit{gap:.25rem}.cell.-gallery{grid-column:1/-1;display:grid;grid-template-columns:repeat(2, 1fr);row-gap:var(--margin-row)}.cell.-gallery.-small{row-gap:var(--grid-gap)}.cell.-grid{display:grid;grid-template-columns:repeat(2, 1fr);align-items:center}.cell.-grid.-small{row-gap:unset}.cell.-head{padding-bottom:var(--section-row)}.cell.-head.-small{padding-bottom:var(--margin-row)}.cell.-list{grid-column:1/-1;display:grid;align-items:center;grid-template-columns:repeat(2, 1fr);padding:var(--margin-row) 0;border-top:2px solid var(--border-color)}.cell.-list .-between{align-items:center}.cell.-null{visibility:hidden;pointer-events:none}.cell.-sticky{position:sticky;top:5rem}@media screen and (max-width: 820px){.cell{grid-column:1/-1}.cell.-gallery,.cell.-grid,.cell.-list{grid-template-columns:100%}.cell.-sticky{position:relative;top:auto}.cell.-null{display:none}}a.cell.-list .text{opacity:.5}@media screen and (max-width: 820px){a.cell.-list .text{opacity:1}}.-hover.cell.-list .text{opacity:1}