section{z-index:1;display:grid;grid-template-columns:repeat(2, 1fr);align-items:flex-start;-moz-column-gap:var(--grid-gap);column-gap:var(--grid-gap);width:100%;padding:var(--section-row) var(--margin);background:var(--background-color)}section.-cover{position:sticky;z-index:-1;display:flex;align-items:center;top:0;left:0;width:100%;height:100vh}section.-cover video{position:absolute;left:50%;width:100%;height:100%;background-image:unset;outline:unset;transform:translateX(-50%);-o-object-fit:cover;object-fit:cover;-o-object-position:50% 50%;object-position:50% 50%;pointer-events:none;filter:brightness(80%)}section.-hidden{overflow:hidden}@media screen and (max-width: 820px){section{grid-template-columns:100%}}