.fab{position:fixed;z-index:999;display:flex;align-items:center;justify-content:center;bottom:1rem;padding:1rem;border-radius:.25rem;line-height:1;text-transform:uppercase;text-wrap:nowrap;will-change:transform;overflow:hidden;cursor:pointer}.fab.-contact{left:50%;color:var(--foreground-color);background:rgba(var(--black-rgb), 0.5);-webkit-backdrop-filter:blur(0.5em);backdrop-filter:blur(0.5em);font-size:var(--font-label-size);font-weight:var(--font-label-weight);letter-spacing:var(--font-label-spacing);transition:all 1s var(--ease)}.fab.-contact div{display:flex;align-items:center;width:8em;will-change:transform;animation:textSlideIn 4s linear infinite}.fab.-contact div span{display:block;width:8em;font-weight:525;text-align:center;text-transform:uppercase}.fab.-scroll{right:1rem;width:2.5rem;height:2.5rem;color:var(--background-color);background:var(--foreground-color);font-size:var(--font-size);font-weight:900;letter-spacing:var(--font-spacing);transition:all .4s var(--ease)}@keyframes textSlideIn{0%{transform:translate3d(0, 0, 0)}100%{transform:translate3d(-100%, 0, 0)}}